"use client";

import { useEffect, useMemo, useState } from "react";
import Link from "next/link";
import toast, { Toaster } from "react-hot-toast";
import {
  Users,
  Clock,
  BadgeAlert,
  RefreshCw,
  Calendar,
  Download,
  Search,
} from "lucide-react";

/* If you have shadcn card/separator components available at these paths, we use them.
   If your project uses different paths, update the imports accordingly.
*/
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Separator } from "@/components/ui/separator";

/* ---- Sample data (replace with real API calls) ---- */
const SAMPLE = [
  { id: 3, empId: "E003", name: "Manav Soni", dept: "Sales", desig: "BDE", firstPunch: "04:31 PM", lastPunch: "-", totalHours: "02:11m", breakHours: "-" },
  { id: 16, empId: "E016", name: "Himanshu", dept: "Sales", desig: "BDM", firstPunch: "04:22 PM", lastPunch: "04:24 PM", totalHours: "00:02m", breakHours: "-" },
  { id: 5, empId: "E005", name: "Sanket Chudegra", dept: "Sales", desig: "BDE", firstPunch: "03:20 PM", lastPunch: "-", totalHours: "03:20m", breakHours: "-" },
  { id: 20, empId: "E020", name: "Dhanmadeep", dept: "Sales", desig: "BDE", firstPunch: "01:00 PM", lastPunch: "-", totalHours: "05:54m", breakHours: "-" },
  { id: 10, empId: "E010", name: "Zuber Kokkuwala", dept: "Sales", desig: "BDE", firstPunch: "10:25 AM", lastPunch: "03:38 PM", totalHours: "04:13m", breakHours: "-" },
];

const KPI_CARDS = [
  { title: "Total Employees", value: 32, icon: Users },
  { title: "Currently Working", value: 6, icon: Users },
  { title: "On Break", value: 1, icon: Clock },
  { title: "Time Off", value: 0, icon: BadgeAlert },
  { title: "Pending Biometrics", value: 24, icon: BadgeAlert },
];

export default function DashboardPage() {
  const [date, setDate] = useState(() => new Date().toISOString().slice(0, 10));
  const [shiftTab, setShiftTab] = useState<"all" | "morning" | "general" | "night" | "default">("all");
  const [query, setQuery] = useState("");
  const [rows, setRows] = useState(SAMPLE);
  const [page, setPage] = useState(1);
  const pageSize = 10;
  const [loading, setLoading] = useState(false);

  /* ---- fetchData: simulate / replace with your API call ---- */
  const fetchData = async () => {
    try {
      setLoading(true);
      // Replace with real API: await fetch(`/api/attendance?date=${date}&shift=${shiftTab}`)
      await new Promise((r) => setTimeout(r, 450));
      setRows(SAMPLE); // replace with API response
      toast.success("Welcome to the Dashboard");
      setPage(1);
    } catch (err) {
      console.error(err);
      toast.error("Failed to fetch data");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [date, shiftTab]);

  const filtered = useMemo(() => {
    const q = query.trim().toLowerCase();
    return rows.filter((r) =>
      !q || r.name.toLowerCase().includes(q) || r.empId.toLowerCase().includes(q) || r.dept.toLowerCase().includes(q)
    );
  }, [rows, query]);

  const totalPages = Math.max(1, Math.ceil(filtered.length / pageSize));
  const pageRows = filtered.slice((page - 1) * pageSize, page * pageSize);

  /* ---- CSV export (current visible page) ---- */
  const exportCSV = () => {
    try {
      const headers = ["Emp ID", "Name", "Dept", "Designation", "First Punch", "Last Punch", "Total Working Hours", "Total Break Hours"];
      const body = pageRows.map(r => [r.empId, r.name, r.dept, r.desig, r.firstPunch, r.lastPunch, r.totalHours, r.breakHours]);
      const csv = [headers, ...body].map(row => row.map(cell => `"${String(cell).replace(/"/g, '""')}"`).join(",")).join("\n");
      const blob = new Blob([csv], { type: "text/csv;charset=utf-8;" });
      const url = URL.createObjectURL(blob);
      const a = document.createElement("a");
      a.href = url;
      a.download = `attendance_${date}.csv`;
      a.click();
      URL.revokeObjectURL(url);
      toast.success("CSV exported");
    } catch (err) {
      console.error(err);
      toast.error("CSV export failed");
    }
  };

  return (
    <>
      {/* Toaster: you can remove this if you already added Toaster in layout.tsx */}
      <Toaster position="top-right" />

      <div className="w-full px-4 py-4">

        {/* Top header */}
        <div className="flex items-start justify-between">
          <div>
            <h1 className="text-2xl font-bold">Dashboard Overview</h1>
            <p className="text-slate-600 text-sm mt-1">Quick summary of today’s activity</p>
          </div>

          <div className="flex items-center gap-2">
            <div className="flex items-center border rounded-md px-2 py-2 bg-white">
              <Calendar className="h-5 w-5 text-slate-600" />
              <input
                type="date"
                value={date}
                onChange={(e) => setDate(e.target.value)}
                className="bg-transparent outline-none ml-1 text-sm"
              />
            </div>

            <button
              onClick={fetchData}
              className="flex items-center gap-2 px-3 py-2 rounded-md bg-white border text-sm"
              aria-label="Refresh"
              title="Refresh"
            >
              <RefreshCw className="w-4 h-4" /> Refresh
            </button>

            <button
              onClick={exportCSV}
              className="flex items-center gap-2 px-3 py-2 rounded-md bg-slate-800 text-white text-sm"
              aria-label="Export CSV"
              title="Export CSV"
            >
              <Download className="w-4 h-4" /> Export CSV
            </button>
          </div>
        </div>

        {/* KPI row: using shadcn Card for consistent look */}
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-5 gap-4 mt-5">
          {KPI_CARDS.map((kpi, i) => {
            const Icon = kpi.icon;
            return (
              <Card key={i} className="p-3">
                <CardHeader className="flex items-center justify-between pb-0">
                  <CardTitle className="text-sm text-slate-500">{kpi.title}</CardTitle>
                  <Icon className="h-6 w-6 text-slate-400" />
                </CardHeader>
                <CardContent className="pt-2">
                  <div className="text-xl font-bold">{kpi.value}</div>
                </CardContent>
              </Card>
            );
          })}
        </div>

        <Separator className="my-6" />

        {/* Attendance summary panel */}
        <div className="bg-white rounded-lg border p-4">
          <div className="flex items-center justify-between">
            <h2 className="font-semibold text-base">Quick Attendance Summary</h2>

            <div className="relative">
              <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-slate-400" />
              <input
                placeholder="Search name / emp id"
                value={query}
                onChange={(e) => { setQuery(e.target.value); setPage(1); }}
                className="pl-9 pr-3 py-2 border rounded-md text-sm"
                aria-label="Search"
              />
            </div>
          </div>

          {/* tabs */}
          <div className="mt-3 border-b">
            <ul className="flex gap-4 text-sm">
              <li>
                <button onClick={() => setShiftTab("all")} className={`pb-2 ${shiftTab === "all" ? "border-b-2 border-blue-600 text-blue-600" : "text-slate-500"}`}>All ({rows.length})</button>
              </li>
              <li>
                <button onClick={() => setShiftTab("morning")} className={`pb-2 ${shiftTab === "morning" ? "border-b-2 border-blue-600 text-blue-600" : "text-slate-500"}`}>Morning</button>
              </li>
              <li>
                <button onClick={() => setShiftTab("general")} className={`pb-2 ${shiftTab === "general" ? "border-b-2 border-blue-600 text-blue-600" : "text-slate-500"}`}>General</button>
              </li>
              <li>
                <button onClick={() => setShiftTab("night")} className={`pb-2 ${shiftTab === "night" ? "border-b-2 border-blue-600 text-blue-600" : "text-slate-500"}`}>Night</button>
              </li>
              <li>
                <button onClick={() => setShiftTab("default")} className={`pb-2 ${shiftTab === "default" ? "border-b-2 border-blue-600 text-blue-600" : "text-slate-500"}`}>Default</button>
              </li>
            </ul>
          </div>

          {/* mini stat boxes */}
          <div className="mt-4 grid grid-cols-3 gap-3">
            <div className="bg-slate-50 border rounded-md p-3">
              <p className="text-xs text-slate-500">Checked In</p>
              <div className="text-lg font-semibold mt-1">7</div>
            </div>
            <div className="bg-slate-50 border rounded-md p-3">
              <p className="text-xs text-slate-500">Not in Yet</p>
              <div className="text-lg font-semibold mt-1">25</div>
            </div>
            <div className="bg-slate-50 border rounded-md p-3">
              <p className="text-xs text-slate-500">Time Off</p>
              <div className="text-lg font-semibold mt-1">0</div>
            </div>
          </div>

          {/* table */}
          <div className="mt-4 overflow-x-auto">
            <table className="w-full text-left text-sm">
              <thead className="text-slate-500">
                <tr>
                  <th className="py-2">Emp ID</th>
                  <th className="py-2">Name</th>
                  <th className="py-2">Dept.</th>
                  <th className="py-2">Designation</th>
                  <th className="py-2">First Punch</th>
                  <th className="py-2">Last Punch</th>
                  <th className="py-2">Total Working Hours</th>
                  <th className="py-2">Total Break Hours</th>
                </tr>
              </thead>

              <tbody>
                {loading ? (
                  <tr><td colSpan={8} className="py-6 text-center">Loading…</td></tr>
                ) : pageRows.length === 0 ? (
                  <tr><td colSpan={8} className="py-6 text-center">No records found.</td></tr>
                ) : (
                  pageRows.map((r) => (
                    <tr key={r.id} className="border-b last:border-b-0">
                      <td className="py-2">{r.empId}</td>
                      <td className="py-2"><Link href={`/employees/${r.id}`} className="hover:underline">{r.name}</Link></td>
                      <td className="py-2">{r.dept}</td>
                      <td className="py-2">{r.desig}</td>
                      <td className="py-2">{r.firstPunch}</td>
                      <td className="py-2">{r.lastPunch}</td>
                      <td className="py-2 text-blue-600 underline">{r.totalHours}</td>
                      <td className="py-2">{r.breakHours}</td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>

          {/* pagination */}
          <div className="mt-4 flex items-center justify-between text-sm">
            <div>Showing {(page - 1) * pageSize + 1}–{Math.min(page * pageSize, filtered.length)} of {filtered.length}</div>

            <div className="flex items-center gap-2">
              <button onClick={() => setPage((p) => Math.max(1, p - 1))} disabled={page === 1} className="px-3 py-1 border rounded disabled:opacity-50">Prev</button>
              <div className="px-3 py-1 border rounded">{page} / {totalPages}</div>
              <button onClick={() => setPage((p) => Math.min(totalPages, p + 1))} disabled={page === totalPages} className="px-3 py-1 border rounded disabled:opacity-50">Next</button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
